
.Form {
    background-color: white;
    width: 1000px;
    height: 1250px;
    margin: auto;
    border-radius: 15px;
    font-weight: 700;
    font-size: 24px;
    color: white;
    position: relative;
    top:50px

}
.Form input{

    line-height: 40px;
    left: 290px;
    margin: 60px 0 0px 0;
    width: 400px;
    color: black;
    position: relative;
    border-radius: 20px;
    text-align: center;
    transform: scale(1.6);


}
.Form button{
    position: relative;
    width: 300px;
    height: 60px;
    margin: 70px 0px 0px 350px;
    background-color: rgba(219, 26, 26, 0.87);
    border-radius: 20px;
    color: white;
    letter-spacing: 5px;
    text-decoration: none;
    font-size: 24px;

}
